<template>

<el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="网站名称">
      <el-input v-model="form.name"></el-input>
    </el-form-item>
    <el-form-item label="网站地址">
      <el-input v-model="form.url"></el-input>
    </el-form-item>
    <el-form-item label="一级目录">
      <el-select v-model="form.first" placeholder="请选择目录" @change="firstChange(form.first)">
        <el-option label="学习" value="learn"></el-option>
        <el-option label="娱乐" value="entertain"></el-option>
        <el-option label="工具" value="tool"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="二级目录">
      <el-select v-model="form.second" placeholder="请选择目录">
        <el-option v-for="item in form.item1" :key="item.id" :label="item.secondtype" :value="item.secondtype"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit">立即创建</el-button>
      <el-button @click="exit1">取消</el-button>
    </el-form-item>
  </el-form>
      
</template>

<script>
import axios from 'axios';
import router from '@/router';


    export default {
      data() {
        return {
          form: {
            name: '',
            first: '',
            second: '',
            date1: '',
            date2: '',
            url: '',
            delivery: false,
            item1: [],
            resource: '',
            desc: '',
            aaa:''
          }
        }
      },
      methods: {
        onSubmit() {
          
          switch(this.form.first){
          //学习类目的请求
            case "learn":
              axios.post( 
                'http://120.76.158.39:8080/insertSite_Learn',
                { 
                  name:this.form.name,
                  url:this.form.url,
                  type:this.form.second
                }).then((response)=>{
                  this.$message(response.data);
                })
                break;

            case "entertain":
                axios.post(
                'http://120.76.158.39:8080/insertSite_Entertain',
                { 
                  name:this.form.name,
                  url:this.form.url,
                  type:this.form.second
                }).then((response)=>{
                  this.$message(response.data);
                })
                break;
            
            case "tool":
                axios.post(
                'http://120.76.158.39:8080/insertSite_Tool',
                { 
                  name:this.form.name,
                  url:this.form.url,
                  type:this.form.second
                }).then((response)=>{
                  this.$message(response.data);

                })
                break;
                
              }
            
        },
        firstChange(first){
          switch(first)
          {
            case "learn":
                  axios.post( 
                      'http://120.76.158.39:8080/getSite_Type',
                      { 
                        type:"学习"
                      }).then((response)=>{
                        this.form.item1 = response.data
                      })
                  break;

            case "entertain":
                  axios.post( 
                      'http://120.76.158.39:8080/getSite_Type',
                      { 
                        type:"娱乐"
                      }).then((response)=>{
                        this.form.item1 = response.data
                      })
                  break;

            case "tool":
                  axios.post( 
                      'http://120.76.158.39:8080/getSite_Type',
                      { 
                        type:"工具"
                      }).then((response)=>{
                        this.form.item1 = response.data
                      })
                  break;
          }
        },
        exit1(){
          router.push("/main/learn");
        }
      }
    }
  </script>