<template>
  <el-container class="a1">
    <el-aside width="35%" style="background-color: #f2f2f2; display: flex; justify-content: center; align-items: center;">
      <div class="login-container">
        <h2 class="login-title">登录</h2>
        <form @submit.prevent="handleLogin">
          <div class="form-group">
            <label for="login-username" class="form-label">用户名:</label>
            <input type="text" id="login-username" v-model="username" placeholder="请输入用户名" :class="{ 'is-invalid': usernameError }">
            <transition name="fade">
              <div v-if="usernameError" class="invalid-feedback">{{ usernameError }}</div>
            </transition>
          </div>
          <div class="form-group">
            <label for="login-password" class="form-label">密码:</label>
            <input type="password" id="login-password" v-model="password" placeholder="请输入密码" :class="{ 'is-invalid': passwordError }">
            <transition name="fade">
              <div v-if="passwordError" class="invalid-feedback">{{ passwordError }}</div>
            </transition>
          </div>
          <button type="submit" class="login-submit-btn" :disabled="isLoading">
            {{ isLoading? '登录中...' : '登录' }}
          </button>
        </form>
      </div>
    </el-aside>
    <el-main style="padding: 0;">
      <el-image
        style="width: 100%; height: 100vh;"
        :src="require('../assets/image/background.jpg')"
        fit="fill"></el-image>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      username: '',
      password: '',
      usernameError: '',
      passwordError: '',
      isLoading: false
    };
  },

  methods: {
    resetFormErrors() {
      this.usernameError = '';
      this.passwordError = '';
    },

    handleLogin() {
      if (this.validateForm()) {
        this.login();
      }
    },

    validateForm() {
      let isValid = true;
      if (!this.username) {
        this.usernameError = '用户名不能为空';
        isValid = false;
      } else {
        this.usernameError = '';
      }
      if (!this.password) {
        this.passwordError = '密码不能为空';
        isValid = false;
      } else {
        this.passwordError = '';
      }
      return isValid;
    },

    login() {
      this.isLoading = true;
      axios.post("http://120.76.158.39:8080/login", {
        username: this.username,
        password: this.password
      })
      .then(response => {
          if(!response.data) {
            this.passwordError = '用户名或密码错误';
            this.isLoading = false;
            return;
          }else{
            console.log('登录成功', response.data);
            // 假设这里有一个方法来处理登录成功后的跳转
            this.$router.push('/main');
            localStorage.setItem('token', response.data.token);
          }
         
        })
      .catch(error => {
          console.error('登录失败', error);
          this.isLoading = false;
          if (error.response && error.response.status === 401) {
            this.passwordError = '用户名或密码错误';
          } else {
            this.passwordError = '登录失败，请稍后重试';
          }
        })
      .finally(() => {
          this.isLoading = false;
        });
    }
  }
};
</script>

<style scoped>
.a1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.login-container {
  background-color: white;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  width: 300px;
  max-width: 90%;
  animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.login-title {
  color: #333;
  margin-bottom: 20px;
  text-align: center;
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 1px;
}

.form-group {
  margin-bottom: 20px;
}

.form-label {
  display: block;
  margin-bottom: 8px;
  color: #666;
  font-size: 16px;
  font-weight: 500;
}

.form-group input {
  width: 90%;
  padding: 12px;
  border: 1px solid #ced4da;
  border-radius: 5px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.form-group input:focus {
  outline: none;
  border-color: #007BFF;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.2);
}

.is-invalid {
  border-color: #dc3545;
}

.invalid-feedback {
  color: #dc3545;
  font-size: 14px;
  margin-top: 5px;
  animation: fadeInError 0.3s ease;
}

@keyframes fadeInError {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.login-submit-btn {
  background-color: #007BFF;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  width: 100%;
  transition: background-color 0.3s ease;
}

.login-submit-btn:hover {
  background-color: #0056b3;
}
</style>