    <div>
    <!-- 遍历 categoryData 对象 -->
    <div v-for="(values, key) in categoryData" :key="key">
      <!-- 显示分类标题 -->
      <div id="font1">
        <el-divider content-position="left">
          <span class="font2">{{ key }}</span>
        </el-divider>
      </div>
      <!-- 显示该分类下的按钮 -->
      <div id="button">
        <el-row style="width: 950px;" class="container">
          <el-button
            class="buttonstyle"
            type="info"
            plain
            @click="tranToLink(item[1])"
            v-for="(item, i) in values"
            :key="i"
          >
            {{ item[0] }}
          </el-button>
        </el-row>
      </div>
      <br><br>
    </div>
  </div>

    </div>


    
</template>

<script>
import axios from 'axios';
import { Container } from 'element-ui';
export default {
    data: function()
    {
        return{
            temp:[],

            netlist:[],
            
            netlist2:[],

            netlist3:[],

            categoryData: []

        }
    },
    methods: {
        tranToLink(url){
            window.open(url)
        }
    },
      created:function(){
        axios.get("http://120.76.158.39:8080/getSiteTool").then((response)=>{
            this.temp=response.data;
            for(let i=0;i<this.temp.length;i++){

                switch(this.temp[i].type){
                    case "学校工具":
                        this.netlist.push(this.temp[i])
                        break;
                    
                    case "代码工具":
                        this.netlist2.push(this.temp[i])
                        break;

                    case "AI工具":
                        this.netlist3.push(this.temp[i])
                        break;

                }
                console.log(this.temp[i].type)
            }
            

        })

        axios.get("http://120.76.158.39:8080/selectToolCategory").then((response)=>{
            this.categoryData = response.data
            console.log(this.categoryData)
        })
      }
}
</script>

<style>
    .font2{
        text-align: left;
        font-size: 15px;
        margin-bottom: auto;
    }

    #button{
      display: flex;
      align-items: flex-start;
