<template>
    <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="一级目录">
          <el-select v-model="form.first" placeholder="请选择目录" @change="firstChange(form.first)">
            <el-option label="学习" value="learn"></el-option>
            <el-option label="娱乐" value="entertain"></el-option>
            <el-option label="工具" value="tool"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="二级目录">
          <el-select v-model="form.second" placeholder="请选择目录" @change="secondChange(form.second)">
            <el-option v-for="item in form.item1" :key="item.id" :label="item.secondtype" :value="item.secondtype"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="删除网址">
          <el-select v-model="form.delectsite" placeholder="请选择要删除的网址">
            <el-option v-for="item in form.item_site" :key="item.id" :label="item.name" :value="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">立即删除</el-button>
          <el-button @click="exit1">取消</el-button>
        </el-form-item>
      </el-form>
          
</template>
    
<script>
import axios from 'axios';
import router from '@/router';

    export default {
        data() {
        return {
            form: {
            name: '',
            first: '',
            second: '',
            date1: '',
            date2: '',
            url: '',
            delivery: false,
            item1: [],
            item_site: [],
            delectsite: '',
            resource: '',
            desc: '',
            aaa:''
            }
        }
        },
        methods: {
        onSubmit() {
            switch(this.form.first){
            //学习类目的请求
            //请求url对不上也会出现跨域报错
            case "learn":
                axios.post( 
                'http://120.76.158.39:8080/deleteSite_Learn',
                { 
                    name:this.form.delectsite
                }).then((response)=>{
                    this.$message(response.data);
                })
                break;
                            
            case "entertain":
                axios.post(
                'http://120.76.158.39:8080/deleteSite_Entertain',
                { 
                    name:this.form.delectsite
                }).then((response)=>{
                    this.$message(response.data);
                })
                break;
            
            case "tool":
                axios.post(
                'http://120.76.158.39:8080/deleteSite_Tool',
                { 
                    name:this.form.delectsite
                }).then((response)=>{
                    this.$message(response.data);

                })
                break;
                
                }
            
        },
        firstChange(first){
            switch(first)
            {
            case "learn":
                    axios.post( 
                        'http://120.76.158.39:8080/getSite_Type',
                        { 
                        type:"学习"
                        }).then((response)=>{
                        this.form.item1 = response.data
                        })
                    break;

            case "entertain":
                    axios.post( 
                        'http://120.76.158.39:8080/getSite_Type',
                        { 
                        type:"娱乐"
                        }).then((response)=>{
                        this.form.item1 = response.data
                        })
                    break;

            case "tool":
                    axios.post( 
                        'http://120.76.158.39:8080/getSite_Type',
                        { 
                        type:"工具"
                        }).then((response)=>{
                            this.$message(response.data)
                        })
                    break;
            }
        },
        secondChange(second){
            switch(this.form.first)
            {
                case "learn":
                    axios.post( 
                        'http://120.76.158.39:8080/selectTypeSite_Learn',
                        { 
                            type:second
                        }).then((response)=>{
                            this.form.item_site = response.data;
                        })
                        break;
            
                case "entertain":
                    axios.post( 
                        'http://120.76.158.39:8080/selectTypeSite_Entertain',
                        { 
                            type:second
                        }).then((response)=>{
                            this.form.item_site = response.data;
                        })
                        break;

                case "tool":
                    axios.post( 
                        'http://120.76.158.39:8080/selectTypeSite_Tool',
                        { 
                            type:second
                        }).then((response)=>{
                            this.form.item_site = response.data;
                        })
                        break;
               

                        
            }
        },
        exit1(){
            router.push("/main/learn");
        }
        
        }
    }
</script>