  <script>
import router from '@/router';
import axios from 'axios';
import { Container } from 'element-ui';
import { RouterView } from 'vue-router';

    export default {
      data() {
        const item = {

        };
        return {
          username: '',
          password: ''
        }
      },
      methods: {
        handleCommand(command) {
          console.log(command);
            switch (command) {
              case "add":
                  if (this.$route.path !== '/main/add') {
                                  router.push('/main/add').catch(err => {
                      if (err.name !== 'NavigationDuplicated') {
                        console.error(err);
                      }
                });
                  }
                  break;
                                
                case "delect":
                  if (this.$route.path !== '/main/delect') {
                                  router.push('/main/delect').catch(err => {
                  if (err.name !== 'NavigationDuplicated') {
                    console.error(err);
                  }
                });
                  }
                  break;
                        }
                    },
        closeLoginModal() {
          this.$store.commit('hideLoginModal');
        },
        handleLogin() {

          axios.post("http://120.76.158.39:8080/login", {
            username: this.username,
            password: this.password
          }).then((response) => {
            console.log(response.data.id);
            console.log(response.data.username)
            localStorage.setItem('token', response.data.token);
            this.$store.state.dispatch('axioaLogin')
          }).catch((error) => {
            console.error("登录失败:", error);
          });
        
          

        }

        
        
  

      }
    };
